import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	 components:{
	        wPicker
	    },
	data() {
		return {
			status:"loading",
			searchVal: "",
			dateText: "按日期筛选",
			startDate:"",
			endDate:"",
			rangeDval:['2019-12-30','2020-01-07'],
		}
	},
	methods: {
		search() {

		},

		datePicker: function(e) {
			const that = this;
			that.$refs.range.show();
		},
		onConfirm(val){
			this.dateText=val.result;
			this.startDate=val.from;
			this.endDate=val.to;
		},
		clearDate(){
			const that = this;
			that.startDate="";
			that.endDate="";
			that.dateText="按日期筛选";
		},
		gotoDetail(id){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/returnVisit/detail",
				query:{
					id:id
				}
			})
		},
		gotoAdd(){
			const that =this;
			that.$Router.push({
				path:"/pages/my/manageNavs/returnVisit/add"
			})
		}
	}
}
